import { type ClassValue, clsx } from 'clsx';
import { twMerge } from 'tailwind-merge';

/**
 * üî• Í∏∞Í∞ÄÏ∞®Îìú Ïú†Ìã∏Î¶¨Ìã∞: Tailwind ÌÅ¥ÎûòÏä§ Î≥ëÌï©
 * clsxÏôÄ tailwind-mergeÎ•º Í≤∞Ìï©ÌïòÏó¨ Ï°∞Í±¥Î∂Ä ÌÅ¥ÎûòÏä§ÏôÄ Ï§ëÎ≥µ Ï†úÍ±∞Î•º ÎèôÏãúÏóê Ï≤òÎ¶¨
 */
export function cn(...inputs: ClassValue[]): string {
  return twMerge(clsx(inputs));
}

/**
 * üî• Í∏∞Í∞ÄÏ∞®Îìú Ïú†Ìã∏Î¶¨Ìã∞: ÌÉÄÏûÖ ÏïàÏ†ÑÌïú ÌÇ§ Ï∂îÏ∂ú
 */
export function getObjectKeys<T extends Record<string, unknown>>(obj: T): Array<keyof T> {
  return Object.keys(obj) as Array<keyof T>;
}

/**
 * üî• Í∏∞Í∞ÄÏ∞®Îìú Ïú†Ìã∏Î¶¨Ìã∞: ÎîúÎ†àÏù¥ Ìï®Ïàò
 */
export function delay(ms: number): Promise<void> {
  return new Promise(resolve => setTimeout(resolve, ms));
}

/**
 * üî• Í∏∞Í∞ÄÏ∞®Îìú Ïú†Ìã∏Î¶¨Ìã∞: ÎîîÎ∞îÏö¥Ïä§
 */
export function debounce<T extends unknown[]>(
  func: (...args: T) => void,
  waitMs: number
): (...args: T) => void {
  let timeoutId: NodeJS.Timeout | null = null;
  
  return (...args: T): void => {
    if (timeoutId) {
      clearTimeout(timeoutId);
    }
    
    timeoutId = setTimeout(() => {
      func(...args);
    }, waitMs);
  };
}

/**
 * üî• Í∏∞Í∞ÄÏ∞®Îìú Ïú†Ìã∏Î¶¨Ìã∞: Ìè¨Îß∑ÌåÖ
 */
export function formatNumber(num: number): string {
  return new Intl.NumberFormat('ko-KR').format(num);
}

export function formatDate(date: Date): string {
  return new Intl.DateTimeFormat('ko-KR', {
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  }).format(date);
}

/**
 * üî• Í∏∞Í∞ÄÏ∞®Îìú Ïú†Ìã∏Î¶¨Ìã∞: ÌÉÄÏûÖ Í∞ÄÎìú
 */
export function isString(value: unknown): value is string {
  return typeof value === 'string';
}

export function isNumber(value: unknown): value is number {
  return typeof value === 'number' && !isNaN(value);
}

export function isArray<T>(value: unknown): value is T[] {
  return Array.isArray(value);
}
