'use client';

// üî• global Ìè¥Î¶¨ÌïÑ Ï∂îÍ∞Ä (ÏµúÏö∞ÏÑ†)
if (typeof global === 'undefined') {
  (globalThis as any).global = globalThis;
}

import { Inter } from 'next/font/google';
import { ReactNode, useState, useLayoutEffect } from 'react';
import { usePathname } from 'next/navigation';
import { AppSidebar } from '../components/layout/AppSidebar';
import { AppHeader } from '../components/layout/AppHeader';
import { MonitoringProvider } from '../contexts/GlobalMonitoringContext';
import { ThemeProvider } from '../providers/ThemeProvider';
import { Logger } from '../../shared/logger';
import './globals.css';

// üî• Í∏∞Í∞ÄÏ∞®Îìú Í∑úÏπô: Inter Ìè∞Ìä∏ ÏµúÏ†ÅÌôî
const inter = Inter({ 
  subsets: ['latin'],
  display: 'swap',
  preload: true
});

// üî• Í∏∞Í∞ÄÏ∞®Îìú Í∑úÏπô: ÌîÑÎ¶¨Ïª¥ÌååÏùºÎêú Ïä§ÌÉÄÏùº ÏÉÅÏàò
const LAYOUT_STYLES = {
  html: 'h-full',
  body: 'h-full bg-slate-50 dark:bg-slate-900 antialiased',
  container: 'h-screen flex',
  sidebar: 'flex-shrink-0',
  main: 'flex-1 flex flex-col overflow-hidden',
  header: 'flex-shrink-0',
  content: 'flex-1 overflow-auto',
} as const;

// üî• Í∏∞Í∞ÄÏ∞®Îìú Í∑úÏπô: Î©îÌÉÄÎç∞Ïù¥ÌÑ∞ ÌÉÄÏûÖ Ï†ïÏùò
interface RootLayoutProps {
  readonly children: ReactNode;
}

export default function RootLayout({ children }: RootLayoutProps): React.ReactElement {
  // üî• ÌïòÏù¥ÎìúÎ†àÏù¥ÏÖò ÏïàÏ†ÑÌïú ÏÇ¨Ïù¥ÎìúÎ∞î ÏÉÅÌÉú Ï¥àÍ∏∞Ìôî (ÏÑúÎ≤ÑÏôÄ ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÎèôÏùº)
  const [sidebarCollapsed, setSidebarCollapsed] = useState<boolean>(false);
  const [isClientMounted, setIsClientMounted] = useState<boolean>(false);
  const pathname = usePathname();

  // üî• DOM ÏóÖÎç∞Ïù¥Ìä∏ Ï†ÑÏóê localStorageÏóêÏÑú ÏÇ¨Ïù¥ÎìúÎ∞î ÏÉÅÌÉú Ï¶âÏãú Î≥µÏõê
  useLayoutEffect(() => {
    if (typeof window !== 'undefined') {
      try {
        const savedState = localStorage.getItem('sidebar-collapsed');
        if (savedState === 'true') {
          setSidebarCollapsed(true);
        }
        Logger.debug('LAYOUT', 'Sidebar state restored immediately', { collapsed: savedState === 'true' });
      } catch (error) {
        Logger.error('LAYOUT', 'Failed to restore sidebar state', error);
      }
    }
    setIsClientMounted(true);
  }, []);

  const handleNavigate = (href: string): void => {
    // Next.js App RouterÎäî ÏûêÎèôÏúºÎ°ú ÎÑ§ÎπÑÍ≤åÏù¥ÏÖòÏùÑ Ï≤òÎ¶¨Ìï©ÎãàÎã§
    window.location.href = href;
  };

  const handleToggleSidebar = (): void => {
    const newState = !sidebarCollapsed;
    setSidebarCollapsed(newState);
    
    // üî• localStorageÏóê ÏÉÅÌÉú Ï†ÄÏû• (ÏùºÍ¥ÄÎêú ÌÇ§ ÏÇ¨Ïö©)
    if (typeof window !== 'undefined') {
      try {
        localStorage.setItem('sidebar-collapsed', newState.toString());
        Logger.debug('LAYOUT', 'Sidebar state saved', { collapsed: newState });
      } catch (error) {
        Logger.error('LAYOUT', 'Failed to save sidebar state', error);
      }
    }
  };

  return (
    <html lang="ko" className={`${inter.className} ${LAYOUT_STYLES.html}`}>
      <head>
        <meta charSet="utf-8" />
        <meta name="viewport" content="width=device-width, initial-scale=1" />
        <meta name="description" content="Loop - AI Í∏∞Î∞ò ÌÉÄÏù¥Ìïë Î∂ÑÏÑù ÎèÑÍµ¨" />
        <title>Loop</title>
      </head>
      <body className={LAYOUT_STYLES.body}>
        <ThemeProvider defaultTheme="system">
          <MonitoringProvider>
            <div className={LAYOUT_STYLES.container}>
              {/* ÏÇ¨Ïù¥ÎìúÎ∞î */}
              <aside className={LAYOUT_STYLES.sidebar}>
                <AppSidebar 
                  activeRoute={pathname}
                  onNavigate={handleNavigate}
                  collapsed={sidebarCollapsed}
                  onToggleCollapse={handleToggleSidebar}
                />
              </aside>

              {/* Î©îÏù∏ ÏΩòÌÖêÏ∏† */}
              <main className={LAYOUT_STYLES.main}>
                {/* Ìó§Îçî */}
                <header className={LAYOUT_STYLES.header}>
                  <AppHeader />
                </header>

                {/* ÌéòÏù¥ÏßÄ ÏΩòÌÖêÏ∏† */}
                <div className={LAYOUT_STYLES.content}>
                  {children}
                </div>
              </main>
            </div>
          </MonitoringProvider>
        </ThemeProvider>
      </body>
    </html>
  );
}
