'use client';

import { forwardRef, useState, ReactNode } from 'react';
import { Eye, EyeOff, Search, X } from 'lucide-react';
import { Logger } from '../../../shared/logger';

// üî• Í∏∞Í∞ÄÏ∞®Îìú Í∑úÏπô: ÌîÑÎ¶¨Ïª¥ÌååÏùºÎêú Ïä§ÌÉÄÏùº ÏÉÅÏàò
const INPUT_STYLES = {
  base: 'flex h-10 w-full rounded-md border border-slate-300 bg-white px-3 py-2 text-sm ring-offset-white file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-slate-500 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 dark:border-slate-700 dark:bg-slate-900 dark:text-slate-50 dark:ring-offset-slate-950 dark:placeholder:text-slate-400 dark:focus:ring-blue-400',
  sizes: {
    sm: 'h-8 text-xs',
    md: 'h-10 text-sm',
    lg: 'h-12 text-base',
  },
  variants: {
    default: 'border-slate-300 focus:ring-blue-500',
    error: 'border-red-300 focus:ring-red-500 focus:border-red-500',
    success: 'border-green-300 focus:ring-green-500 focus:border-green-500',
  },
  withIcon: {
    left: 'pl-10',
    right: 'pr-10',
  },
  container: 'relative w-full',
  iconContainer: {
    left: 'absolute left-3 top-1/2 transform -translate-y-1/2 text-slate-400 dark:text-slate-500',
    right: 'absolute right-3 top-1/2 transform -translate-y-1/2 text-slate-400 dark:text-slate-500',
  },
  iconButton: 'absolute right-3 top-1/2 transform -translate-y-1/2 text-slate-400 hover:text-slate-600 dark:text-slate-500 dark:hover:text-slate-300 cursor-pointer transition-colors',
} as const;

// üî• Í∏∞Í∞ÄÏ∞®Îìú Í∑úÏπô: Î™ÖÏãúÏ†Å ÌÉÄÏûÖ Ï†ïÏùò
export interface InputProps extends Omit<React.InputHTMLAttributes<HTMLInputElement>, 'size'> {
  readonly size?: keyof typeof INPUT_STYLES.sizes;
  readonly variant?: keyof typeof INPUT_STYLES.variants;
  readonly leftIcon?: ReactNode;
  readonly rightIcon?: ReactNode;
  readonly clearable?: boolean;
  readonly label?: string;
  readonly error?: string;
  readonly helperText?: string;
  readonly required?: boolean;
  readonly onClear?: () => void;
}

function cn(...classes: (string | undefined | false | null)[]): string {
  return classes.filter(Boolean).join(' ');
}

export const Input = forwardRef<HTMLInputElement, InputProps>(
  ({ 
    className,
    type = 'text',
    size = 'md',
    variant = 'default',
    leftIcon,
    rightIcon,
    clearable = false,
    label,
    error,
    helperText,
    required = false,
    disabled,
    value,
    onChange,
    onClear,
    ...props 
  }, ref) => {
    
    const [showPassword, setShowPassword] = useState<boolean>(false);
    const [focused, setFocused] = useState<boolean>(false);

    const isPassword = type === 'password';
    const hasValue = value !== undefined && value !== '';
    const showClearButton = clearable && hasValue && !disabled;
    const showPasswordToggle = isPassword && !disabled;

    const inputClassName = cn(
      INPUT_STYLES.base,
      INPUT_STYLES.sizes[size],
      INPUT_STYLES.variants[error ? 'error' : variant],
      leftIcon ? INPUT_STYLES.withIcon.left : false,
      (rightIcon || showClearButton || showPasswordToggle) ? INPUT_STYLES.withIcon.right : false,
      className
    );

    const handleClear = (): void => {
      if (onClear) {
        onClear();
      } else if (onChange) {
        // ÌÅ¥Î¶¨Ïñ¥ Ïù¥Î≤§Ìä∏ ÏãúÎÆ¨Î†àÏù¥ÏÖò
        const event = {
          target: { value: '' },
          currentTarget: { value: '' },
        } as React.ChangeEvent<HTMLInputElement>;
        onChange(event);
      }
      Logger.debug('INPUT', 'Input cleared');
    };

    const togglePasswordVisibility = (): void => {
      setShowPassword(!showPassword);
      Logger.debug('INPUT', `Password visibility toggled: ${!showPassword}`);
    };

    const handleFocus = (event: React.FocusEvent<HTMLInputElement>): void => {
      setFocused(true);
      props.onFocus?.(event);
    };

    const handleBlur = (event: React.FocusEvent<HTMLInputElement>): void => {
      setFocused(false);
      props.onBlur?.(event);
    };

    const actualType = isPassword && showPassword ? 'text' : type;

    const labelId = label ? `input-label-${Math.random().toString(36).substr(2, 9)}` : undefined;
    const errorId = error ? `input-error-${Math.random().toString(36).substr(2, 9)}` : undefined;
    const helperId = helperText ? `input-helper-${Math.random().toString(36).substr(2, 9)}` : undefined;

    return (
      <div className="w-full">
        {/* ÎùºÎ≤® */}
        {label && (
          <label 
            id={labelId}
            htmlFor={props.id}
            className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2"
          >
            {label}
            {required && <span className="text-red-500 ml-1">*</span>}
          </label>
        )}

        {/* Input Container */}
        <div className={INPUT_STYLES.container}>
          {/* Ï¢åÏ∏° ÏïÑÏù¥ÏΩò */}
          {leftIcon && (
            <div className={INPUT_STYLES.iconContainer.left}>
              {leftIcon}
            </div>
          )}

          {/* Input Field */}
          <input
            ref={ref}
            type={actualType}
            className={inputClassName}
            disabled={disabled}
            value={value}
            onChange={onChange}
            onFocus={handleFocus}
            onBlur={handleBlur}
            aria-labelledby={labelId}
            aria-describedby={cn(
              errorId && errorId,
              helperId && helperId
            ) || undefined}
            aria-invalid={!!error}
            aria-required={required}
            {...props}
          />

          {/* Ïö∞Ï∏° Ïï°ÏÖò Î≤ÑÌäºÎì§ */}
          <div className={INPUT_STYLES.iconContainer.right}>
            <div className="flex items-center gap-1">
              {/* ÌÅ¥Î¶¨Ïñ¥ Î≤ÑÌäº */}
              {showClearButton && (
                <button
                  type="button"
                  className={cn(INPUT_STYLES.iconButton, 'p-0.5')}
                  onClick={handleClear}
                  aria-label="ÏûÖÎ†• ÎÇ¥Ïö© ÏßÄÏö∞Í∏∞"
                  tabIndex={-1}
                >
                  <X className="h-4 w-4" />
                </button>
              )}

              {/* Ìå®Ïä§ÏõåÎìú ÌÜ†Í∏Ä */}
              {showPasswordToggle && (
                <button
                  type="button"
                  className={cn(INPUT_STYLES.iconButton, 'p-0.5')}
                  onClick={togglePasswordVisibility}
                  aria-label={showPassword ? 'Ìå®Ïä§ÏõåÎìú Ïà®Í∏∞Í∏∞' : 'Ìå®Ïä§ÏõåÎìú Î≥¥Í∏∞'}
                  tabIndex={-1}
                >
                  {showPassword ? (
                    <EyeOff className="h-4 w-4" />
                  ) : (
                    <Eye className="h-4 w-4" />
                  )}
                </button>
              )}

              {/* Ïö∞Ï∏° ÏïÑÏù¥ÏΩò */}
              {rightIcon && !showClearButton && !showPasswordToggle && rightIcon}
            </div>
          </div>
        </div>

        {/* ÏóêÎü¨ Î©îÏãúÏßÄ */}
        {error && (
          <p 
            id={errorId}
            className="mt-2 text-sm text-red-600 dark:text-red-400"
            role="alert"
          >
            {error}
          </p>
        )}

        {/* ÎèÑÏõÄÎßê ÌÖçÏä§Ìä∏ */}
        {helperText && !error && (
          <p 
            id={helperId}
            className="mt-2 text-sm text-slate-500 dark:text-slate-400"
          >
            {helperText}
          </p>
        )}
      </div>
    );
  }
);

Input.displayName = 'Input';

// üî• Í≤ÄÏÉâ Ï†ÑÏö© Input Ïª¥Ìè¨ÎÑåÌä∏
export const SearchInput = forwardRef<HTMLInputElement, Omit<InputProps, 'leftIcon' | 'type'>>(
  (props, ref) => {
    return (
      <Input
        ref={ref}
        type="search"
        leftIcon={<Search className="h-4 w-4" />}
        placeholder="Í≤ÄÏÉâ..."
        clearable
        {...props}
      />
    );
  }
);

SearchInput.displayName = 'SearchInput';
